	export default {
		data() {
			return {
				address:[],
				useUser:this.user,
				goShopping:null,
				shop:null,
				txt:[],
				jg:null,
				liuyan:'',
				url:this.url1
			}
		},
		onLoad(op) {
			this.goShopping=JSON.parse(op.goShopping)
			this.shop=JSON.parse(op.shop)
			this.txt=JSON.parse(op.txt)
			for(let i=0;i<this.txt.length;i++){
				this.jg+=new Number(this.txt[i].jg)
			}
			
			console.log(typeof this.goShopping.freight,this.jg)
		},
		onShow(){
			this.req()
		},
		methods: {
			liji:function(){
				if(this.address.length>0){
					let that=this;
					let usurl=that.url1+"/dinghai/ShopOrder/insertAddress";
					uni.showLoading({  mask: true,   title: '正在购买中'  });
					uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'POST',
						data: {
							shopAddressName:that.address[0].userName,
							shopAddress:that.address[0].addressOne+that.address[0].addressTwo,
							shopAdressTel:that.address[0].userTel,
							shopName:that.shop.name,
							shopOrderNum:1,
							shopPrice:that.jg,
							shopFreight:that.goShopping.freight,
							message:that.liuyan,
							Coupon:0,
							uid:that.useUser.uid,
							sid:that.shop.uid,
							commodityid:that.goShopping.id,
							commodityName:that.goShopping.name
						},
					success: (res) => {
						console.log(res)
						if(res.statusCode==200){
							uni.request({
								url: that.url1+"/dinghai/ShopOrder/pay",
								header:{
									"Content-Type" : "application/x-www-form-urlencoded"
								},
								method:'POST',
								data: {
									uid:that.useUser.uid,
									sid:that.shop.uid,
									money:that.jg+that.goShopping.freight
								},
							success: (res) => {
								console.log(res)
								if(res.statusCode==200){
									if(res.data.flag==200){
										uni.showToast({
											title:"支付成功！",
											icon:"none"
										});
										setTimeout(function(){
											uni.navigateBack({
												
											})
										},1000)
									}else{
										uni.showToast({
											title:"支付失败！",
											icon:"none"
										});
										uni.showModal({
										    title: '提示',
										    content: '您的余额不足是否要充值',
										    success: function (res) {
										        if (res.confirm) {
										            uni.navigateTo({
										            	url:'../../../mes/jine/chongzhi/chongzhi'
										            })
										        } else if (res.cancel) {
													
										        }
										    }
										});
										
									}
								}else{
									uni.showToast({
										title:"您的网络打了个盹！",
										icon:"none"
									});
								}
							},
							fail : function(err){
								uni.showToast({
									title:"您的网络打了个盹！",
									icon:"none"
								});
							}
							})
							
						}else{
							uni.showToast({
								title:"您的网络打了个盹！",
								icon:"none"
							});
						}
					},
					fail : function(err){
						uni.showToast({
							title:"您的网络打了个盹！",
							icon:"none"
						});
					}
					})
				}else{
					uni.showToast({
						title:'请添加地址后购买',
						icon:'none'
					})
				}
			},
			tjsh:function(){
				uni.navigateTo({
					url:'../../../mes/dizhi/tjdizhi/tjdizhi'
				})
			},
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			dizhiXuanZe:function(){
				
			},
			req:function(){
				uni.showLoading({
					title:"正在加载",
					mask:true
				})
				let that=this;
				let url = that.url1+"/dinghai/Address/selectAddress";
				uni.request({
					 url : url,
							 header : {
								 "Content-type" : "application/x-www-form-urlencoded"
							 },
							 method : 'post',
							 data : {
								 userId:that.useUser.uid
							 },
							 success : function(res){
							     if(res.statusCode===200){
									 that.address=res.data.flag
									 console.log(that.address)
									 uni.showToast({
									 	title:"加载成功"
									 })
								 }else{
									 uni.showToast({
									 	title:"您的网络开了小差",
										icon:"none"
									 })
								 }
								 console.log(that.address)
								 console.log(that.address.length)
								 if(that.address.length >=1 ){
								 	that.judgehava = false;
								 }else{
								 	that.judgehava = true;
								 }
							 },
							 fail : function(err){
								 console.log(err);
								 uni.showToast({
								 	title:"连接服务器失败",
									icon:"none"
								 })
							 }
				});
				
			},
		 }
}
